<h3 className="">{"Leading"}</h3>
{"\n"}
<p className="">{"By default, snackbars are centered horizontally within the viewport. On larger\nscreens, they can optionally be displayed on the leading edge of the screen\n(the left side in LTR, or the right side in RTL). Set the "}
<code className="">{"leading"}</code>
{"\nattribute or property to enable this layout."}</p>
